import React, { useEffect } from "react";
import { useProfit } from "../hooks/useProfit";
import "aos/dist/aos.css";
import AOS from "aos";

function PartnerShip() {
  const { profit, loading, error } = useProfit();

  useEffect(() => {
    AOS.init({
      duration: 1000,
      once: true,
    });
  }, []);

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!profit) return null;

  return (
    <div>
      <section className="py-20 px-10 bg-white" id="profit">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          {/* Title Section */}
          <div className="text-center mb-10" data-aos="fade-up">
            <h2 className={`${
              profit.subtitle 
                ? "text-4xl font-semibold text-black mb-4" 
                : "text-4xl font-semibold text-black"
            }`}>
              {profit.title}
            </h2>
            {profit.subtitle && (
              <p className="mt-4 text-lg font-normal text-gray-600">
                {profit.subtitle}
              </p>
            )}
          </div>

          {/* Cards Section */}
          <div
            className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8"
            data-aos="fade-up"
          >
            {profit.items.map((item, index) => (
              <div
                key={item.id}
                className="bg-ungu p-5 rounded-lg shadow-sm border border-gray-300 h-full transition-all duration-300 hover:shadow-xl"
                data-aos="fade-up"
                data-aos-delay={index * 100}
              >
                <h3 className="text-2xl text-white font-bold mb-4">
                  {item.title}
                </h3>
                <div
                  className="text-white"
                  dangerouslySetInnerHTML={{ __html: item.description }}
                />
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}

export default PartnerShip;
