import { useState, useEffect } from 'react';
import axios from 'axios';

const BASE_API_URL = process.env.REACT_APP_BASE_API_URL;

export const useJoin = () => {
    const [joinData, setJoinData] = useState(null);
    const [contactData, setContactData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const [joinResponse, contactResponse] = await Promise.all([
                    axios.get(`${BASE_API_URL}/api/home/`),
                    axios.get(`${BASE_API_URL}/api/contact/`)
                ]);

                setJoinData(joinResponse.data.join);
                setContactData(contactResponse.data);
                setLoading(false);
            } catch (error) {
                setError(error);
                setLoading(false);
            }
        };

        fetchData();
    }, []);

    return { joinData, contactData, loading, error };
};
