import React from "react";
import { useSettings } from '../hooks/useSettings';
import { useContact } from '../hooks/useContact';

const BASE_URL = process.env.REACT_APP_BASE_API_URL;

function Footer() {
  const { settings, loading: settingsLoading, error: settingsError } = useSettings();
  const { contact, loading: contactLoading, error: contactError } = useContact();

  if (settingsLoading || contactLoading) return null;
  if (settingsError || contactError) return null;
  if (!settings || !contact) return null;

  const currentYear = new Date().getFullYear();

  const handleWhatsAppClick = () => {
    const whatsappUrl = `${contact.whatsapp_link}${contact.whatsapp_number}`;
    window.open(whatsappUrl, '_blank');
  };

  const handleTelegramClick = () => {
    window.open(contact.telegram_link, '_blank');
  };

  return (
    <div>
      <footer className="bg-gray-900 text-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
            {/* Company Info */}
            <div>
              <div className="h-12 mb-4 relative">
                <img
                  src={`${BASE_URL}${settings.logo}`}
                  alt={settings.title}
                  className="h-full w-auto object-contain brightness-0 invert"
                  onError={(e) => {
                    e.target.onerror = null;
                    e.target.src = '/placeholder-logo.png'; // Fallback logo jika error
                  }}
                />
              </div>
              <p className="text-gray-400">{settings.title}</p>
              {settings.gmaps_link && (
                <a 
                  href={settings.gmaps_link} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="text-gray-400 hover:text-white transition-colors duration-300 mt-2 block"
                >
                  <i className="fas fa-map-marker-alt mr-2"></i>
                  View on Maps
                </a>
              )}
            </div>

            {/* Quick Links */}
            <div>
              <h3 className="text-lg font-semibold mb-4">Quick Links</h3>
              <ul className="space-y-2">
                <li>
                  <a href="#about" className="text-gray-400 hover:text-white transition-colors duration-300">
                    About Us
                  </a>
                </li>
                <li>
                  <a href="#products" className="text-gray-400 hover:text-white transition-colors duration-300">
                    Products
                  </a>
                </li>
                <li>
                  <a href="#gallery" className="text-gray-400 hover:text-white transition-colors duration-300">
                    Gallery
                  </a>
                </li>
                <li>
                  <a href="#contact" className="text-gray-400 hover:text-white transition-colors duration-300">
                    Contact
                  </a>
                </li>
              </ul>
            </div>

            {/* Contact Info */}
            <div>
              <h3 className="text-lg font-semibold mb-4">{contact.title}</h3>
              <p className="text-gray-400 mb-4">{contact.subtitle}</p>
              <ul className="space-y-2">
                <li className="text-gray-400 flex items-center gap-2">
                  <i className="fab fa-whatsapp w-4"></i>
                  <span>{contact.whatsapp_number}</span>
                </li>
              </ul>
            </div>

            {/* Social Links */}
            <div>
              <h3 className="text-lg font-semibold mb-4">Connect With Us</h3>
              <div className="flex flex-col space-y-3">
                <button 
                  onClick={handleWhatsAppClick}
                  className="text-gray-400 hover:text-green-500 transition-colors duration-300 flex items-center gap-2"
                >
                  <i className="fab fa-whatsapp text-xl"></i>
                  <span>WhatsApp</span>
                </button>
                <button 
                  onClick={handleTelegramClick}
                  className="text-gray-400 hover:text-blue-500 transition-colors duration-300 flex items-center gap-2"
                >
                  <i className="fab fa-telegram text-xl"></i>
                  <span>Telegram</span>
                </button>
              </div>
            </div>
          </div>

          {/* Copyright */}
          <div className="border-t border-gray-800 mt-12 pt-8 text-center text-gray-400">
            <p>&copy; {currentYear} {settings.title}. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default Footer;