import React from "react";
import { useContact } from '../hooks/useContact';
import "aos/dist/aos.css";

function Contact() {
  const { contact, loading, error } = useContact();

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!contact) return null;

  const handleOrderClick = (e) => {
    e.preventDefault();
    if (contact?.whatsapp_link) {
      window.open(contact.whatsapp_link, '_blank');
    }
  };

  const handleTelegramClick = () => {
    window.open(contact.telegram_link, '_blank');
  };

  return (
    <div>
      <section className="pt-64 pb-32 bg-white text-center overflow-hidden" id="contact">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          {/* Title */}
          <div className="mb-12" data-aos="fade-up">
            <h2 className="text-4xl font-bold text-navy mb-4">{contact.title}</h2>
            <div className="w-10 h-1 bg-orange-500 mx-auto rounded-full"></div>
          </div>

          {/* Info Pemesanan */}
          <div className="bg-gray-50 rounded-lg shadow-lg p-8 mb-8 mx-auto max-w-4xl" data-aos="fade-up">
            <div className="flex flex-col md:flex-row md:items-center md:justify-between">
              {/* Title Info Pemesanan */}
              <div className="flex items-center mb-8 md:mb-0">
                <div className="w-12 h-12 bg-ungu text-white flex items-center justify-center rounded-lg mr-4">
                  <i className="fas fa-phone text-xl"></i>
                </div>
                <h3 className="text-xl font-bold text-ungu">Info Pemesanan</h3>
              </div>

              {/* Contact Buttons */}
              <div className="flex flex-col md:flex-row gap-4">
                <button 
                  onClick={handleOrderClick}
                  className="w-full md:w-auto bg-ungu text-white py-3 px-6 rounded-lg flex items-center justify-center hover:bg-ungu/90 transition duration-300">
                  <i className="fab fa-whatsapp mr-2"></i>{contact.whatsapp_number}
                </button>
                <button 
                  onClick={handleTelegramClick}
                  className="w-full md:w-auto bg-blue-500 text-white py-3 px-6 rounded-lg flex items-center justify-center hover:bg-blue-600 transition duration-300">
                  <i className="fab fa-telegram mr-2"></i>Telegram
                </button>
              </div>
            </div>
          </div>

          {/* Format Order */}
          <div className="bg-gray-50 rounded-lg shadow-xl p-8 mx-auto max-w-4xl" data-aos="fade-up">
            <div className="flex flex-col md:flex-row items-center md:gap-12">
              <div className="flex items-center shrink-0 self-center mb-6 md:mb-0">
                <div className="w-12 h-12 bg-ungu text-white flex items-center justify-center rounded-full mr-4">
                  <i className="fas fa-file-alt text-xl"></i>
                </div>
                <h3 className="text-xl font-bold text-ungu">Format Order</h3>
              </div>
              <div 
                className="text-left text-black w-full md:w-auto"
                dangerouslySetInnerHTML={{ __html: contact.format_order }}
              />
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Contact;