import { useEffect } from 'react';
import Glide from '@glidejs/glide';

export const useGlideSlider = (selector, options = {}) => {
    useEffect(() => {
        const slider = new Glide(selector, {
            type: 'carousel',
            autoplay: 5000,
            hoverpause: true,
            perView: 1,
            animationDuration: 800,
            gap: 0,
            ...options
        }).mount();

        return () => {
            slider.destroy();
        };
    }, [selector, options]);
}; 