import React, { useState } from 'react';
import { useContact } from '../hooks/useContact';
import { motion, AnimatePresence } from 'framer-motion';
import { X, MessageCircle, Phone, Send } from 'lucide-react';

function FloatingButton() {
  const { contact } = useContact();
  const [isOpen, setIsOpen] = useState(false);

  const buttonVariants = {
    hidden: { 
      opacity: 0,
      y: 20,
      scale: 0.8,
      transition: { duration: 0.3 }
    },
    visible: (custom) => ({
      opacity: 1,
      y: custom,
      scale: 1,
      transition: { 
        type: "spring",
        stiffness: 300,
        damping: 20,
        delay: custom === -180 ? 0.1 : 0.2
      }
    }),
    exit: { 
      opacity: 0,
      y: 20,
      scale: 0.8,
      transition: { duration: 0.3 }
    },
    hover: {
      scale: 1.1,
      transition: { 
        type: "spring",
        stiffness: 400,
        damping: 10
      }
    },
    tap: {
      scale: 0.9
    }
  };

  const mainButtonVariants = {
    closed: { 
      rotate: 0,
      scale: 1,
      backgroundColor: "rgb(37, 99, 235)" // blue-600
    },
    open: { 
      rotate: 135,
      scale: 1,
      backgroundColor: "rgb(239, 68, 68)" // red-500
    }
  };

  const iconVariants = {
    closed: { 
      rotate: 0,
      scale: 1
    },
    open: { 
      rotate: 90,
      scale: 1
    }
  };

  return (
    <div className="fixed right-6 bottom-6 flex items-end justify-end z-50">
      <AnimatePresence mode="sync">
        {isOpen && (
          <>
            {/* WhatsApp Button */}
            <motion.a
              href={contact?.whatsapp_link}
              target="_blank"
              rel="noopener noreferrer"
              variants={buttonVariants}
              initial="hidden"
              animate="visible"
              exit="exit"
              whileHover="hover"
              whileTap="tap"
              custom={-65}
              className="w-14 h-14 rounded-full bg-green-500 text-white flex items-center justify-center shadow-lg absolute right-0 cursor-pointer"
            >
              <Phone className="w-6 h-6" />
              <span className="absolute right-16 bg-black text-white px-4 py-2 rounded-lg opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap text-sm">
                WhatsApp
              </span>
            </motion.a>

            {/* Telegram Button */}
            <motion.a
              href={contact?.telegram_link}
              target="_blank"
              rel="noopener noreferrer"
              variants={buttonVariants}
              initial="hidden"
              animate="visible"
              exit="exit"
              whileHover="hover"
              whileTap="tap"
              custom={-130}
              className="w-14 h-14 rounded-full bg-blue-500 text-white flex items-center justify-center shadow-lg absolute right-0 cursor-pointer"
            >
              <Send className="w-6 h-6" />
              <span className="absolute right-16 bg-black text-white px-4 py-2 rounded-lg opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap text-sm">
                Telegram
              </span>
            </motion.a>
          </>
        )}
      </AnimatePresence>

      {/* Main Toggle Button */}
      <motion.button
        variants={mainButtonVariants}
        animate={isOpen ? "open" : "closed"}
        transition={{ 
          type: "spring",
          stiffness: 300,
          damping: 20
        }}
        onClick={() => setIsOpen(!isOpen)}
        className="w-14 h-14 rounded-full text-white flex items-center justify-center shadow-lg z-10 hover:shadow-xl transition-shadow"
      >
        <motion.div
          variants={iconVariants}
          animate={isOpen ? "open" : "closed"}
          transition={{ duration: 0.3 }}
        >
          {isOpen ? <X className="w-6 h-6" /> : <MessageCircle className="w-6 h-6" />}
        </motion.div>
      </motion.button>
    </div>
  );
}

export default FloatingButton;