import React, { useEffect, useState } from "react";
import { useProducts } from "../hooks/useProducts";
import { useContact } from "../hooks/useContact";
import Glide from "@glidejs/glide";
import "aos/dist/aos.css";
import "@glidejs/glide/dist/css/glide.core.min.css";
import "@glidejs/glide/dist/css/glide.theme.min.css";

function Product() {
  const { products, loading, error } = useProducts();
  const [showAll] = useState(false);
  const { contact } = useContact();

  useEffect(() => {
    if (products?.categories?.length > 3) {
      const slider = new Glide("#products-slider", {
        type: "carousel",
        perView: 2,
        autoplay: 5000,
        hoverpause: true,
        gap: 80,
        breakpoints: {
          1024: { perView: 2 },
          640: { perView: 1 },
        },
      }).mount();

      return () => slider.destroy();
    }
  }, [products]);

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!products) return null;

  const handleOrderClick = (e) => {
    e.preventDefault();
    if (contact?.whatsapp_link) {
      window.open(contact.whatsapp_link, '_blank');
    }
  };

  return (
    <div>
      <section className="py-20 bg-gray-50" id="products">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          {/* Header Section */}
          <div className="text-center mb-16" data-aos="fade-up">
            <h2 className="text-4xl font-bold text-navy">{products.title}</h2>
            <div className="w-10 h-1 bg-orange-500 rounded-full mt-4 flex flex-wrap mx-auto"></div>
          </div>

          {/* Product Cards */}
          <div
            className={products.categories.length > 3 ? "glide" : ""}
            id="products-slider"
          >
            <div
              className={products.categories.length > 3 ? "glide__track" : ""}
              data-glide-el="track"
            >
              <div
                className={`${
                  products.categories.length > 3
                    ? "glide__slides"
                    : "grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8"
                }`}
              >
                {products.categories.map((category, index) => (
                  <div
                    key={category.id}
                    className={`${
                      products.categories.length > 3 ? "glide__slide" : ""
                    }`}
                  >
                    <div
                      className="bg-white rounded-xl shadow-lg hover:shadow-2xl transition-transform transform hover:scale-105 border border-gray-200 overflow-hidden flex flex-col h-[600px]"
                      data-aos="fade-up"
                      data-aos-delay={index * 100}
                    >
                      {/* Card Header */}
                      <div className="p-6 bg-gradient-to-r from-navy/10 to-navy/30 border-b border-gray-200">
                        <h3 className="text-lg font-bold text-gray-800 text-center">
                          {category.title}
                        </h3>
                      </div>

                      {/* Product List - Scrollable Container */}
                      <div className="flex-1 p-6 overflow-y-auto scrollbar-thin scrollbar-thumb-navy/60 scrollbar-track-gray-100">
                        <ul className="space-y-4">
                          {category.items
                            .sort((a, b) => a.order - b.order)
                            .slice(showAll)
                            .map((item) => (
                              <li
                                key={item.id}
                                className="flex justify-between items-center hover:bg-gray-100 p-3 rounded-lg transition-colors duration-200"
                              >
                                <div className="flex-1 min-w-0 mr-4">
                                  <h4 className="text-gray-900 font-medium truncate">
                                    {item.title || "-"}
                                  </h4>
                                  <p className="text-sm text-gray-500 truncate">
                                    {item.jumlah || "-"}
                                  </p>
                                </div>
                                <div className="text-right flex-shrink-0">
                                  <p className="text-md font-medium text-navy whitespace-nowrap">
                                    {item.harga ? `Rp ${parseInt(item.harga).toLocaleString()}` : "-"}
                                  </p>
                                </div>
                              </li>
                            ))}
                        </ul>
                      </div>

                      {/* Card Footer */}
                      <div className="p-4 bg-gray-50 border-t border-gray-200 text-center">
                      <button 
                        onClick={handleOrderClick}
                        className="w-full bg-navy hover:bg-navy/90 text-white py-3 rounded-lg font-medium transition-all duration-300">
                        Kontak Kami
                      </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Product;