import React, { useState, useEffect } from "react";
import { useSettings } from "../hooks/useSettings";
import { useContact } from "../hooks/useContact";
import { Link } from "react-scroll";

function Navbar() {
  const { settings, loading } = useSettings();
  const { contact } = useContact();
  const [scrolled, setScrolled] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [activeLink, setActiveLink] = useState("hero-slider");

  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 10;
      setScrolled(isScrolled);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const handleOrderClick = (e) => {
    e.preventDefault();
    if (contact?.whatsapp_link) {
      window.open(contact.whatsapp_link, '_blank');
    }
  };

  return (
    <nav
      className={`top-0 left-0 right-0 z-40 bg-white transition-shadow duration-300 ${
        scrolled ? "shadow-md" : "shadow-sm"
      }`}
    >
      <div className="max-w-8xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-[7rem] items-center">
          <div className="flex-shrink-0">
            {!loading && settings && (
              <div className="h-12 w-auto relative">
                <h1 className="font-extrabold text-ungu text-[1.5rem] md:text-[2rem]">
                  {settings.title}
                </h1>
              </div>
            )}
          </div>

          {/* Navigation */}
          <div className="hidden md:flex items-center gap-8">
            {[
              { to: "hero-slider", label: "Home" },
              { to: "about", label: "Tentang Kami" },
              { to: "products", label: "Daftar Harga" },
              { to: "testimoni", label: "Testimonial" },
              { to: "contact", label: "Kontak" },
            ].map((link) => (
              <Link
                key={link.to}
                to={link.to}
                smooth={true}
                duration={500}
                onSetActive={() => setActiveLink(link.to)}
                className={`border-transparent inline-flex items-center px-1 pt-1 border-b-2 text-md font-medium transition-colors duration-300 cursor-pointer ${
                  activeLink === link.to
                    ? "text-gray-400 border-gray-400"
                    : "text-black hover:text-gray-400"
                }`}
              >
                {link.label}
              </Link>
            ))}

            <button
              onClick={handleOrderClick}
              className="rounded-sm bg-ungu text-white px-6 py-3 text-sm font-medium cursor-pointer"
            >
              ORDER
            </button>
          </div>

          {/* Burger Menu */}
          <div className="md:hidden">
            <button
              className="text-ungu focus:outline-none"
              onClick={() => setMenuOpen(!menuOpen)}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-8 w-8"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                strokeWidth={2}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
            </button>
          </div>
        </div>
      </div>

      {/* Mobile Menu */}
      <div
        className={`fixed top-0 left-0 h-full bg-white w-64 transform transition-transform duration-300 z-[60] ${
          menuOpen ? "translate-x-0" : "-translate-x-full"
        } shadow-lg`}
      >
        {/* Overlay */}
        {menuOpen && (
          <div 
            className="fixed inset-0 bg-black bg-opacity-50 z-[50]" 
            onClick={() => setMenuOpen(false)}
          />
        )}

        {/* Menu Container */}
        <div className="relative h-full z-[70] bg-white">
          {/* Close Button */}
          <button
            className="absolute top-4 right-4 text-gray-600 focus:outline-none"
            onClick={() => setMenuOpen(false)}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              strokeWidth={2}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>

          {/* Menu Content */}
          <div className="mt-16 flex flex-col items-start px-6 space-y-4">
            {[
              { to: "hero-slider", label: "Home" },
              { to: "about", label: "Tentang Kami" },
              { to: "products", label: "Daftar Harga" },
              { to: "testimoni", label: "Testimonial" },
              { to: "contact", label: "Kontak" },
            ].map((link) => (
              <Link
                key={link.to}
                to={link.to}
                smooth={true}
                duration={500}
                onClick={() => setMenuOpen(false)}
                className={`text-lg font-medium transition-colors duration-300 cursor-pointer ${
                  activeLink === link.to
                    ? "text-gray-400 border-gray-400"
                    : "text-black hover:text-gray-400"
                }`}
              >
                {link.label}
              </Link>
            ))}

            <button
              onClick={(e) => {
                handleOrderClick(e);
                setMenuOpen(false);
              }}
              className="rounded-md bg-ungu text-white px-6 py-3 text-sm font-medium cursor-pointer w-full text-center"
            >
              ORDER
            </button>
          </div>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
