import React, { useState } from "react";
import { useAbout } from "../hooks/useAbout";
import { useSettings } from "../hooks/useSettings";

const BASE_URL = process.env.REACT_APP_BASE_API_URL;

function About() {
  const { about, loading, error } = useAbout();
  const { settings } = useSettings();
  const [activeTab, setActiveTab] = useState("visi"); // Menentukan tab aktif (Visi atau Misi)

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!about) return null;

  // Menentukan konten aktif (Visi atau Misi)
  const activeContent = activeTab === "visi" ? about.visi : about.misi;

  return (
    <div className="py-20 bg-gray-50 mx-10" id="about">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex flex-col lg:flex-row items-start">
          {/* Konten Kiri */}
          <div className="lg:w-1/2 lg:pr-8">
          <div className="w-max flex items-center gap-5">
            <div className="w-10 h-1 bg-orange-500 rounded-full flex-shrink-0"></div>
            <p className="text-sm text-navy font-semibold">{settings.title}</p>
          </div>
          
            {/* Title Halaman */}
            <h2 className="text-3xl font-bold text-shadow mb-4">{about.title}</h2>
            {/* Subtitle Halaman */}
            <p className="text-md font-normal text-shadow mb-8">{about.subtitle}</p>

            {/* Tombol Visi dan Misi */}
            <div className="flex gap-4 mb-8">
              <button
                onClick={() => setActiveTab("visi")}
                className={`px-9 py-4 text-lg font-light ${
                  activeTab === "visi" ? "bg-oren bg-opacity-75 text-white" : "bg-gray-200 text-gray-700"
                } transition-colors duration-300`}
              >
                Visi
              </button>
              <button
                onClick={() => setActiveTab("misi")}
                className={`px-9 py-4 text-lg font-light ${
                  activeTab === "misi" ? "bg-oren bg-opacity-75 text-white" : "bg-gray-200 text-gray-700"
                } transition-colors duration-300`}
              >
                Misi
              </button>
            </div>

            {/* Konten Dinamis */}
            <div>
              <h3 className="text-2xl font-semibold mb-4">{activeContent.title}</h3>
              {activeTab === "visi" ? (
                <div
                  className="text-gray-700"
                  dangerouslySetInnerHTML={{ __html: activeContent.description }}
                />
              ) : (
                <ul className="list-decimal list-inside text-gray-700">
                  {about.misi.description
                    .match(/<li>(.*?)<\/li>/g)
                    .map((item, index) => (
                      <li key={index} className="mb-2">
                        {item.replace(/<\/?li>/g, "").trim()}
                      </li>
                    ))}
                </ul>
              )}
            </div>
          </div>

          {/* Gambar Kanan */}
          <div className="lg:w-1/2">
            <img
              src={`${BASE_URL}${activeContent.image}`}
              alt={activeContent.title}
              className="w-full h-full object-cover rounded-md shadow-md"
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = '/placeholder-image.jpg'; // Gambar fallback jika error
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default About;
