import React from "react";
import { useJoin } from "../hooks/useJoin";
import { useContact } from "../hooks/useContact";
import "aos/dist/aos.css";

const BASE_URL = process.env.REACT_APP_BASE_API_URL;

function Join() {
  const { joinData, contactData, loading, error } = useJoin();
  const { contact } = useContact();

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!joinData || !contactData) return null;

  const handleOrderClick = (e) => {
    e.preventDefault();
    if (contact?.whatsapp_link) {
      window.open(contact.whatsapp_link, '_blank');
    }
  };

  return (
    <div>
      <section
        className="py-28 px-10 bg-cover bg-center text-white relative"
        style={{
          backgroundImage: joinData.image ? `url(${BASE_URL}${joinData.image})` : 'none',
        }}
        id="join"
      >
        {/* Overlay */}
        <div className="absolute inset-0 bg-[#362562] bg-opacity-95"></div>

        {/* Content */}
        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 z-10">
          <div className="flex flex-col lg:flex-row items-center justify-between gap-8">
            {/* Teks */}
            <div className="text-center lg:text-left" data-aos="fade-right">
              <h2 className="text-4xl font-medium mb-4">{joinData.title}</h2>
              <p className="text-md max-w-3xl">{joinData.subtitle}</p>
            </div>

            {/* Tombol */}
            <div className="flex-shrink-0" data-aos="fade-left">
              <button
                onClick={handleOrderClick}
                className="rounded-full bg-orange-500 text-white px-10 py-5 font-medium hover:bg-orange-600 transition-colors duration-300 flex items-center"
              >
                <i className="text-xl"></i>
                HUBUNGI KAMI
              </button>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Join;
