import React, { useEffect, useRef, useState } from "react";
import { useGallery } from "../hooks/useGallery";
import { ChevronLeft, ChevronRight } from "lucide-react";
import Glide from "@glidejs/glide";
import "aos/dist/aos.css";
import "@glidejs/glide/dist/css/glide.core.min.css";
import "@glidejs/glide/dist/css/glide.theme.min.css";

const BASE_URL = process.env.REACT_APP_BASE_API_URL;

function Gallery() {
  const { gallery, loading, error } = useGallery();
  const sliderRef = useRef(null); // Reference untuk Glide instance
  const startX = useRef(0); // Posisi awal drag
  const endX = useRef(0); // Posisi akhir drag
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    if (gallery?.images?.length > 4) {
      const slider = new Glide("#gallery-slider", {
        type: "carousel",
        perView: 4,
        gap: 15,
        autoplay: 3000,
        hoverpause: true,
        breakpoints: {
          1280: { perView: 4 },
          1024: { perView: 3 },
          768: { perView: 2 },
          640: { perView: 2 },
        },
      });

      slider.mount();
      sliderRef.current = slider;

      return () => slider.destroy();
    }
  }, [gallery]);

  const handleDragStart = (e) => {
    startX.current = e.changedTouches ? e.changedTouches[0].pageX : e.pageX;
  };

  const handleDragEnd = (e) => {
    endX.current = e.changedTouches ? e.changedTouches[0].pageX : e.pageX;
    const diff = endX.current - startX.current;

    if (sliderRef.current) {
      if (diff > 50) {
        sliderRef.current.go("<");
      } else if (diff < -50) {
        sliderRef.current.go(">");
      }
    }
  };

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!gallery) return null;

  return (
    <div>
      <section className="bg-gray-50 pt-20" id="gallery">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-10" data-aos="fade-up">
            <h2 className="text-4xl font-bold text-navy">{gallery.title}</h2>
            <div className="w-10 h-1 bg-orange-500 rounded-full mt-4 flex flex-wrap mx-auto"></div>
          </div>

          {gallery.images.length > 4 ? (
            <div
              className="glide relative"
              id="gallery-slider"
              onTouchStart={handleDragStart} // Untuk perangkat sentuh
              onTouchEnd={handleDragEnd} // Untuk perangkat sentuh
              onMouseDown={handleDragStart} // Untuk perangkat desktop
              onMouseUp={handleDragEnd} // Untuk perangkat desktop
            >
              {/* Controls */}
              <div
                className="glide__arrows absolute inset-0 flex justify-between items-center px-4 z-10"
                data-glide-el="controls"
              >
                <button
                  className="p-2 rounded-full bg-white shadow-lg hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-navy transition-all duration-200"
                  data-glide-dir="<"
                  aria-label="Previous slide"
                >
                  <ChevronLeft className="w-6 h-6 text-navy" />
                </button>
                <button
                  className="p-2 rounded-full bg-white shadow-lg hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-navy transition-all duration-200"
                  data-glide-dir=">"
                  aria-label="Next slide"
                >
                  <ChevronRight className="w-6 h-6 text-navy" />
                </button>
              </div>

              <div className="glide__track" data-glide-el="track">
                <ul className="glide__slides">
                  {gallery.images
                    .sort((a, b) => a.order - b.order)
                    .map((item, index) => (
                      <li
                        key={item.id}
                        className="glide__slide"
                        data-aos="fade-up"
                        data-aos-delay={index * 100}
                      >
                        <div className="relative group overflow-hidden rounded-lg shadow-lg mx-2">
                          <div className="aspect-w-4 aspect-h-5">
                            <img
                              src={`${BASE_URL}${item.image}`}
                              className="w-full h-full object-cover transform"
                              alt={`Gallery ${index + 1}`}
                            />
                            <div className="absolute inset-0 bg-black bg-opacity-40 opacity-0 flex items-center justify-center group-hover:opacity-100 transition-opacity duration-300">
                              <button className="px-6 py-2 bg-red-500 bg-opacity-80 text-white rounded-lg font-medium">
                                View Image
                              </button>
                            </div>
                          </div>
                        </div>
                      </li>
                    ))}
                </ul>
              </div>

              {/* Bullets */}
              <div
                className="glide__bullets mt-6 flex justify-center"
                data-glide-el="controls[nav]"
              >
                {gallery.images.map((_, index) => (
                  <button
                    key={index}
                    className="glide__bullet mx-1 w-3 h-3 rounded-full bg-gray-300 hover:bg-navy focus:outline-none transition-colors duration-200"
                    data-glide-dir={`=${index}`}
                    aria-label={`Go to slide ${index + 1}`}
                  ></button>
                ))}
              </div>
            </div>
          ) : (
            <div className="grid grid-cols-2 gap-3 md:grid-cols-3 lg:grid-cols-4 md:gap-6">
              {gallery.images
                .sort((a, b) => a.order - b.order)
                .slice(0, isMobile ? 2 : 4)
                .map((item, index) => (
                  <div
                    key={item.id}
                    className="relative group overflow-hidden rounded-lg shadow-md hover:shadow-lg transition-shadow duration-300"
                    data-aos="fade-up"
                    data-aos-delay={index * 100}
                  >
                    <div className="aspect-square w-full max-w-[160px] mx-auto md:max-w-none md:aspect-w-4 md:aspect-h-3">
                      <img
                        src={`${BASE_URL}${item.image}`}
                        className="w-full h-full object-cover transform group-hover:scale-110 transition-transform duration-500"
                        alt={`Gallery ${index + 1}`}
                        loading="lazy"
                      />
                      <div className="absolute inset-0 bg-black bg-opacity-40 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-center justify-center">
                        <button className="px-2 py-1 sm:px-4 sm:py-2 md:px-6 md:py-2 bg-white text-gray-900 rounded-lg font-medium transform -translate-y-2 group-hover:translate-y-0 transition-transform duration-300 text-xs sm:text-sm md:text-base">
                          View Image
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
            </div>
          )}
        </div>
      </section>
    </div>
  );
}

export default Gallery;
