import React, { useEffect } from "react";
import { useTestimonials } from "../hooks/useTestimonials";
import { ChevronLeft, ChevronRight } from "lucide-react";
import Glide from "@glidejs/glide";
import "aos/dist/aos.css";
import "@glidejs/glide/dist/css/glide.core.min.css";
import "@glidejs/glide/dist/css/glide.theme.min.css";

const BASE_URL = process.env.REACT_APP_BASE_API_URL;

function Testimoni() {
  const { testimonials, loading, error } = useTestimonials();

  useEffect(() => {
    if (testimonials?.testimonials?.length) {
      const slider = new Glide("#testimonials-slider", {
        type: "carousel",
        perView: 5,
        gap: 15,
        autoplay: 3000,
        hoverpause: true,
        dragThreshold: 80,        // Jarak minimum untuk trigger drag
        swipeThreshold: 40,       // Jarak minimum untuk trigger swipe
        dragDistance: false,      // Menggunakan persentase untuk drag
        animationDuration: 400,   // Durasi animasi lebih cepat untuk drag yang responsif
        touchRatio: 0.5,         // Rasio sensitifitas touch/drag
        touchAngle: 45,          // Sudut maksimum untuk drag
        peek: 0,                 // Mengatur preview slide selanjutnya
        rewind: true,            // Memungkinkan slider kembali ke awal
        breakpoints: {
          1024: { perView: 3 },
          640: { perView: 2 },
        },
      });

      slider.mount();

      return () => slider.destroy();
    }
  }, [testimonials]);

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!testimonials) return null;

  return (
    <div id="testimoni">
      <section className="pt-20 px-10 bg-white" id="testimonials">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16" data-aos="fade-up">
            <h2 className="text-4xl mb-10 font-bold text-navy">
              {testimonials.title}
            </h2>
            <p className="mt-4 text-[17px] font-normal text-navy max-w-md mx-auto">
              {testimonials.subtitle}
            </p>
            <div className="w-10 h-1 bg-orange-500 rounded-full mt-6 flex flex-wrap mx-auto"></div>
          </div>

          <div className="glide relative cursor-grab active:cursor-grabbing" id="testimonials-slider">
            {/* Controls */}
            <div
              className="glide__arrows absolute inset-0 flex justify-between items-center px-4 z-10 pointer-events-none"
              data-glide-el="controls"
            >
              <button
                className="p-2 rounded-full bg-white shadow-lg hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-navy transition-all duration-200 pointer-events-auto"
                data-glide-dir="<"
                aria-label="Previous slide"
              >
                <ChevronLeft className="w-6 h-6 text-navy" />
              </button>
              <button
                className="p-2 rounded-full bg-white shadow-lg hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-navy transition-all duration-200 pointer-events-auto"
                data-glide-dir=">"
                aria-label="Next slide"
              >
                <ChevronRight className="w-6 h-6 text-navy" />
              </button>
            </div>

            <div className="glide__track" data-glide-el="track">
              <ul className="glide__slides select-none">
                {testimonials.testimonials
                  .sort((a, b) => a.order - b.order)
                  .map((item, index) => (
                    <li
                      key={item.id}
                      className="glide__slide"
                      data-aos="fade-up"
                      data-aos-delay={index * 100}
                    >
                      <div className="bg-white rounded-lg shadow-sm border border-gray-100 overflow-hidden h-[500px] flex flex-col">
                        {/* Image Container */}
                        <div className="h-max relative">
                          <img
                            src={`${BASE_URL}${item.image}`}
                            className="w-full h-full object-cover select-none"
                            alt="Testimonial"
                            draggable="false"
                            onError={(e) => {
                              e.target.onerror = null;
                              e.target.src = '/placeholder-testimonial.jpg'; // Fallback image untuk testimonial
                            }}
                          />
                          <div className="absolute inset-0 bg-black opacity-10"></div>
                        </div>
                        {/* Content */}
                        <div className="p-6">
                          <p className="text-gray-600 mb-4 select-none">{item.description}</p>
                          <h3 className="text-lg font-bold text-gray-800 select-none">
                            {item.name}
                          </h3>
                        </div>
                      </div>
                    </li>
                  ))}
              </ul>
            </div>

            {/* Bullets */}
            <div
              className="glide__bullets mt-6 flex justify-center"
              data-glide-el="controls[nav]"
            >
              {testimonials.testimonials.map((_, index) => (
                <button
                  key={index}
                  className="glide__bullet mx-1 w-3 h-3 rounded-full bg-gray-300 hover:bg-navy focus:outline-none transition-colors duration-200"
                  data-glide-dir={`=${index}`}
                  aria-label={`Go to slide ${index + 1}`}
                ></button>
              ))}
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Testimoni;