import React from 'react';
import { useLocation } from 'react-router-dom';

function ProductDetails() {
  const location = useLocation();
  const { category } = location.state || { category: null }; // Get the category from the state

  if (!category) {
    return <div>No product details available.</div>;
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20">
      <h2 className="text-4xl font-extrabold text-red-500">{category.title}</h2>
      <ul className="space-y-4 mt-6">
        {category.items.map((item) => (
          <li key={item.id} className="flex justify-between items-center p-3 border-b border-gray-200">
            <div>
              <h4 className="text-gray-900 font-medium">{item.title}</h4>
              <p className="text-sm text-gray-500">{item.jumlah}</p>
            </div>
            <p className="text-lg font-semibold text-red-400">
              Rp {parseInt(item.harga).toLocaleString()}
            </p>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default ProductDetails; 