import React, { useEffect } from "react";
import { useWhyMe } from "../hooks/useWhyMe";
import AOS from "aos";
import "aos/dist/aos.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fas } from "@fortawesome/free-solid-svg-icons";

library.add(fas);

function WhyChoose() {
  const { whyMe, loading, error } = useWhyMe();

  useEffect(() => {
    AOS.init({
      duration: 1000,
      once: true,
    });
  }, []);

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error.message}</div>;
  if (!whyMe) return null;

  return (
    <div>
      <section className="py-20 bg-white" id="why-choose">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          {/* Title Section */}
          <div
            className="text-center mb-16 w-full flex flex-col items-center"
            data-aos="fade-up"
          >
            <h2 className="text-4xl font-extrabold text-navy w-[65%] mx-auto">
              {whyMe.title}
            </h2>
            <p className="mt-4 text-md font-normal text-navy max-w-xl mx-auto">
              {whyMe.subtitle}
            </p>
            <div className="w-20 h-1 mt-3 bg-orange-500 mx-auto rounded-full"></div>
          </div>

          {/* Items Section */}
          <div
            className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-2 gap-8"
            data-aos="fade-up"
          >
            {whyMe.items.map((item, index) => (
              <div
                key={item.id}
                className="group bg-white p-8 rounded-lg transition-all duration-300 flex items-center hover:bg-gray-50"
                data-aos="fade-up"
                data-aos-delay={index * 100}
              >
                {/* Icon */}
                <div className="w-20 h-20 bg-white group-hover:bg-oren rounded-full flex items-center justify-center mr-6 flex-shrink-0 transition-colors duration-300 text-navy group-hover:text-white">
                  <FontAwesomeIcon icon={item.icon} size="2x" />
                </div>

                {/* Text */}
                <div className="flex-1">
                  <h3 className="text-lg font-bold mb-2 text-navy group-hover:text-oren transition-colors duration-300 line-clamp-2">
                    {item.title}
                  </h3>
                  <div
                    className="text-slate-900 font-semibold line-clamp-3"
                    dangerouslySetInnerHTML={{ __html: item.description }}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}

export default WhyChoose;
