import React, { useEffect } from 'react';
import { useGlideSlider } from '../hooks/useGlideSlider';
import { useContact } from '../hooks/useContact';
import { useSettings } from "../hooks/useSettings";
import AOS from 'aos';
import 'aos/dist/aos.css';

const BASE_URL = process.env.REACT_APP_BASE_API_URL;

function SliderPresentation({ sliders }) {
    useGlideSlider('#hero-slider');
    const { contact } = useContact();
    const { settings } = useSettings();

    useEffect(() => {
        AOS.init({
            duration: 1000,
            once: true
        });
    }, []);

    const handleOrderClick = () => {
        if (contact?.whatsapp_link) {
            window.open(contact.whatsapp_link, '_blank');
        }
    };

    const handleTelegramClick = () => {
        if (contact?.telegram_link) {
            window.open(contact.telegram_link, '_blank');
        }
    };

    return (
        <div className="glide" id="hero-slider">
            <div className="glide__track" data-glide-el="track">
                <ul className="glide__slides">
                    {sliders.map((slider) => (
                        <li key={slider.id} className="glide__slide relative h-[600px]">
                            <img
                                src={`${BASE_URL}${slider.image}`}
                                className="w-full h-[600px] object-cover"
                                alt={slider.title}
                                data-aos="fade-up"
                                onError={(e) => {
                                    e.target.onerror = null;
                                    e.target.src = '/placeholder-slider.jpg';
                                }}
                            />
                            <div className="absolute inset-0 bg-black bg-opacity-70 flex items-center justify-center">
                                <div className="text-center text-white px-4">
                                <div className="w-max mx-auto flex items-center mb-5 gap-5">
                                    <div className="w-10 h-1 bg-orange-500 rounded-full flex-shrink-0"></div>
                                    <p className="text-sm text-white font-semibold">{settings?.title || "-"}</p>
                                </div>
                                    <h1 
                                        className="text-4xl mb-4 font-extrabold max-w-3xl mx-auto"
                                        data-aos="fade-down"
                                        data-aos-delay="200"
                                    >
                                        {slider.title}
                                    </h1>
                                    <p 
                                        className="text-md font-normal mb-8 max-w-3xl mx-auto"
                                        data-aos="fade-up"
                                        data-aos-delay="400"
                                    >
                                        {slider.subtitle}
                                    </p>
                                    <div className="flex gap-2 justify-center">
                                        <button 
                                            onClick={handleOrderClick}
                                            className="border-white bg-oren rounded-full transition-all duration-500 text-white px-8 py-3 text-lg font-medium">
                                            ORDER
                                        </button>
                                        <button
                                            onClick={handleTelegramClick}
                                            className="border-white bg-transparent border-2 hover:bg-oren hover:border-oren rounded-full transition-all duration-300 text-white px-8 py-3 text-lg font-medium">
                                            TELEGRAM
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    );
}

export default SliderPresentation;