import React, { useEffect } from "react";
import { usePackage } from "../hooks/usePackage";
import { useContact } from "../hooks/useContact";
import "aos/dist/aos.css";
import AOS from "aos";

function Package() {
  const { packageData, loading, error } = usePackage();
  const { contact, contactLoading, contactError } = useContact();
  

  useEffect(() => {
    AOS.init({
      duration: 1000,
      once: true,
    });
  }, []);

  if (loading || contactLoading) return <div>Loading...</div>;
  if (error || contactError) return <div>Error: {error?.message || contactError?.message}</div>;
  if (!packageData || !contact) return null;

  const { paket, kategori_paket } = packageData;

  const handleOrderClick = (e) => {
    e.preventDefault();
    if (contact?.whatsapp_link) {
      window.open(contact.whatsapp_link, '_blank');
    }
  };

  return (
    <div>
      <section className="py-20 bg-gray-50" id="packages">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          {/* Header Section */}
          <div className="text-center mb-16" data-aos="fade-up">
            <h2 className="text-4xl font-bold text-navy">{paket.title}</h2>
            <div className="w-10 h-1 bg-orange-500 rounded-full mt-3 flex flex-wrap mx-auto"></div>
            <p className="text-[17px] font-normal text-navy leading-relaxed max-w-[90%] mx-auto">
              {paket.subtitle}
            </p>
          </div>

          {/* Package Cards */}
          <div
            className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8"
            data-aos="fade-up"
          >
            {kategori_paket.map((pkg, index) => (
              <div
                key={pkg.id}
                className="bg-white rounded-lg shadow-lg border border-gray-200 hover:shadow-xl transition-transform transform hover:scale-105 overflow-hidden"
                data-aos="fade-up"
                data-aos-delay={index * 100}
              >
                {/* Card Header */}
                <div className="p-6 border-b border-gray-100 text-center">
                  <h3 className="text-xl font-bold text-gray-800">{pkg.title}</h3>
                  <p className="text-2xl font-bold text-oren mt-2">
                    Rp {parseInt(pkg.price).toLocaleString()}
                    <span className="text-sm font-normal text-gray-600 ml-1">/paket</span>
                  </p>
                </div>

                {/* Card Content */}
                <div className="p-6">
                  <ul className="space-y-2">
                    {pkg.items.map((item) => (
                      <li key={item.id} className="flex items-start">
                        <svg
                          className="w-5 h-5 text-oren mr-2 flex-shrink-0"
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M5 13l4 4L19 7"
                          />
                        </svg>
                        <span className="text-gray-600 text-sm">{item.items}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* Card Footer */}
                <div className="p-6 border-t border-gray-100 text-center">
                  <button 
                    onClick={handleOrderClick}
                    className="w-full bg-navy hover:bg-navy/90 text-white py-3 rounded-lg font-medium transition-all duration-300">
                    Beli Paket
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}

export default Package;
